import React, { Component } from 'react'
import styled from 'styled-components'

const InputField = styled.input`
    text-align:center;
    margin-bottom:2em;
    width:30em;
`

const Flex = styled.div`
    display:${props => props.display || "flex" };
    position: ${props => props.position || "auto" };
    overflow:hidden;
    flex-direction: ${props => props.direction || "column"};
    align-items: ${props => props.alignItems || "center"};
    justify-content: ${props => props.justify || "center"};
    width: ${props => props.width || "auto" };
    top: ${props => props.top || "0" };
`

const ColorBox = styled.span`
    display:flex;
    width:13px;
    height:13px;
    background-color:${props => props.color || "white"};
    border: 1px solid black;
    margin-left:5px;
    border-radius:3px;
    cursor:pointer;
    &:hover {
        border: 3px solid black;
    }
`

export default class SouvenirForm extends Component {

    state={
        name:"",
        location:"",
        color:"",
        audio:""
    }

    pickColor = (color) => {
        this.setState({
            color:color
        })
    }

    handleChange = (e) => {
        this.setState({
            [e.target.id]:e.target.value
        })
        
    }

  render() {
    return (
        <Flex direction="row" width="100vw" position="relative">
            <Flex width="100vw" position="relative">
                <InputField type="text" placeholder="Name" id="name" onChange={this.handleChange}/>
                <InputField type="text" placeholder="Location" id="location" onChange={this.handleChange}/>
                <InputField type="text" placeholder="Add audio link" id="audio" onChange={this.handleChange}/>
                <Flex direction="row">
                    <ColorBox selected={false} color="#DC143C" onClick={() => (this.pickColor("#DC143C"))}/>
                    <ColorBox selected={false} color="#87CEFA" onClick={() => (this.pickColor("#87CEFA"))}/>
                    <ColorBox selected={false} color="#1E90FF" onClick={() => (this.pickColor("#1E90FF"))}/>
                    <ColorBox selected={false} color="#00FF7F" onClick={() => (this.pickColor("#00FF7F"))}/>
                    <ColorBox selected={false} color="#FA8072" onClick={() => (this.pickColor("#FA8072"))}/>
                </Flex>
                <ColorBox color={this.state.color}/>
                <button onClick={() => {console.log("hi")}}>Start writing</button>
                <h3>{this.state.name}</h3>
                <h3>{this.state.location}</h3>
                <h3>{this.state.audio}</h3>
            </Flex>
            <Flex display="flex" width="100vw">Heya</Flex>
        </Flex>
    )
  }
}
